/* 游꿛 ESTILOS GENERALES */
.guild-container {
    min-height: 100vh;
    background-color: #121212;
    color: white;
    padding: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    /* A침adimos las im치genes de fondo */
    background-image: url('../assets/images/estandarte.png'), url('../assets/images/estandarte.png'); /* Imagen de izquierda y derecha */
    background-position: left top, right top; /* Alineamos las im치genes a la parte superior */
    background-repeat: no-repeat; /* Evitamos que se repita la imagen */
    background-size: 15% auto; /* Ajustamos el tama침o de las im치genes al 15% del ancho y 100% del alto */
    background-attachment: fixed; /* Fijamos las im치genes para que no se muevan al hacer scroll */
}

/* 游늷 TITULOS */
.guild-title {
    font-family: 'Folkard', sans-serif;
    font-size: 42px;
    font-weight: bold;
    color: #ffd700;
    margin-bottom: 5px;
}

/* 游늷 IMAGEN DEL TITULO */
.guild-title-image {
    width: 100%;  /* Hace que la imagen ocupe el 100% del ancho del contenedor */
    max-width: 800px; /* Establece un m치ximo para que no sea demasiado grande en pantallas grandes */
    height: auto; /* Mantiene la proporci칩n de la imagen */
    margin-bottom: 10px;
}

.guild-warning {
    font-family: 'Folkard', sans-serif;
    font-size: 22px;
    color: red;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 20px;
}

/* 游늷 CONTENEDORES PRINCIPALES */
.sidebars {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    margin-top: 20px;
    gap: 20px;
    box-sizing: border-box; /* Asegura que el padding no afecte el ancho total */
}

/* 游늷 BARRAS LATERALES */
.left-sidebar, .right-sidebar {
    width: 22%; /* Ajusta la proporci칩n de los sidebars */
    text-align: left;
    padding: 10px;
    background-color: #222;
    border-radius: 8px;
    flex-grow: 0; /* No permite que los sidebars crezcan m치s all치 de su tama침o */
}

/* 游늷 TABLA */
.table-container {
    width: 50%; /* Ajusta el tama침o de la tabla */
    text-align: center;
    background-color: #222;
    border-radius: 8px;
    padding: 10px;
    max-width: 100%; /* Asegura que la tabla no se desborde */
    overflow-x: auto; /* Si la tabla es muy ancha, tendr치 un scroll horizontal */
}

/* 游늷 TABLA - ESTILOS */
.guild-table {
    width: 100%;
    border-collapse: collapse;
    border: 2px solid #555;
    background-color: #222;
}

.guild-table th,
.guild-table td {
    border: 1px solid #444;
    padding: 10px;
    text-align: center;
}

.guild-table th {
    background-color: #333;
    color: #ddd;
    font-weight: bold;
}

.guild-table tr:hover {
    background-color: #444;
}

.dead {
    background-color: rgba(128, 128, 128, 0.3); 
    opacity: 0.6; 
    color: #aaa;
}

.dead:hover {
    background-color: rgba(128, 128, 128, 0.4); 
}
  
.status-cell {
    font-weight: bold;
}  

/* 游늷 IM츼GENES */
.role-image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

/* 游늷 TEXTO TWITCH */
.twitch-text {
    color: #9146ff;
    font-weight: bold;
}

/* 游늷 LISTAS */
.role-list, .class-list, .profession-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.role-list div, .class-list div, .profession-list div {
    color: #ddd;
    margin-bottom: 10px;
}

/* 游님 RESPONSIVE DESIGN */
@media (max-width: 1024px) {
    .guild-title-image {
        max-width: 90%; /* Hace que la imagen sea m치s peque침a en pantallas medianas */
    }

    .sidebars {
        flex-direction: column;
        align-items: center;
    }

    .left-sidebar, .right-sidebar, .table-container {
        width: 100%;
        max-width: 90%;
        margin-bottom: 20px;
    }
}

@media (max-width: 768px) {
    .guild-title-image {
        max-width: 80%; /* En pantallas m치s peque침as, reduce a칰n m치s el tama침o */
    }

    .guild-title {
        font-size: 36px;
    }

    .guild-warning {
        font-size: 20px;
    }

    .table-container {
        overflow-x: auto;
    }

    .guild-table {
        width: 100%;
        min-width: 600px;
    }

    .role-image {
        width: 35px;
        height: 35px;
    }
}

@media (max-width: 480px) {
    .guild-title-image {
        max-width: 70%; /* Ajusta a칰n m치s el tama침o de la imagen para dispositivos peque침os */
    }
    
    .guild-title {
        font-size: 28px;
    }

    .guild-warning {
        font-size: 18px;
    }

    .sidebars {
        flex-direction: column;
        align-items: center;
    }

    .left-sidebar, .right-sidebar {
        width: 100%;
        text-align: center;
    }

    .guild-table {
        width: 100%;
        min-width: 500px;
    }

    .role-image {
        width: 30px;
        height: 30px;
    }
}
