/* src/components/Notice.css */

.notice-container {
    width: 100%;
    height: 50px; /* A침adimos una altura fija para asegurar que el texto sea legible */
    padding: 10px;
    background-color: #222;
    border-radius: 8px;
    margin-top: 20px;
    overflow: hidden; /* Ocultar el contenido fuera de la caja */
    position: relative;
    display: flex; /* Alineamos el contenido de forma flexible */
    align-items: center; /* Centrar verticalmente el texto */
    justify-content: center; /* Centrar horizontalmente el texto si es necesario */
    white-space: nowrap; /* Evitar que el texto se envuelva */
  }
  
  .notice-text {
    color: #ffd700;
    font-size: 18px;
    font-weight: bold;
    position: absolute;
    left: 100%; /* Comienza fuera de la pantalla a la derecha */
    animation: slide 40s linear infinite; /* Animaci칩n que mueve el texto */
    padding-left: 20px; /* Un peque침o espacio al inicio del texto */
  }
  
  @keyframes slide {
    0% {
      left: 100%; /* Comienza fuera de la pantalla a la derecha */
    }
    100% {
      left: -100%; /* Termina fuera de la pantalla a la izquierda */
    }
  }
  
  /* Ajuste para pantallas m치s peque침as (m칩viles) */
  @media (max-width: 768px) {
    .notice-text {
      font-size: 16px; /* Reducir el tama침o de la fuente en pantallas m치s peque침as */
    }
  
    .notice-container {
      padding: 8px; /* Reducir el padding en pantallas m치s peque침as */
      height: 45px; /* Ajustar altura para m칩viles */
    }
  }
  